import { FunctionComponent } from 'preact';
import { classes } from '@adobe-commerce/elsie/lib';
import { Add } from '@adobe-commerce/elsie/icons';
import { Icon } from '@adobe-commerce/elsie/components';
import { useText } from '@adobe-commerce/elsie/i18n';
import '@/requisitionList/components/RequisitionListActions/RequisitionListActions.css';

export interface RequisitionListActionsProps {
  className?: string;
  selectable?: boolean;
  onAddNew?: () => void;
}

export const RequisitionListActions: FunctionComponent<
  RequisitionListActionsProps
> = ({ selectable, className, onAddNew }) => {
  const translations = useText({
    addNewReqListBtn: `RequisitionList.AddNewReqList.addNewReqListBtn`,
  });

  return (
    <button
      type="button"
      aria-label={translations.addNewReqListBtn}
      role="button"
      className={classes([
        'requisition-list-actions',
        ['requisition-list-actions--selectable', selectable],
        className,
      ])}
      data-testid="requisition-list-actions-button"
      onClick={onAddNew}
    >
      <span
        className="requisition-list-actions__title"
        data-testid="requisition-list-actions-button-text"
      >
        {translations.addNewReqListBtn}
      </span>
      <Icon source={Add} size="32" />
    </button>
  );
};
